.next-trips {
  border-right: 1px solid $grey;

  @media (max-width: $tablet-max-width) {
    margin-bottom: 20px;
  }

  @media (max-width: $mobile-max-width) {
    margin-bottom: 30px;
    padding-bottom: 21px;

    text-align: center;

    border-bottom: 1px solid $grey;
    border-right: none;
  }
}

.next-trips__title {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
  color: $darkest;

  @media (max-width: $tablet-max-width) {
    margin-bottom: 19px;
  }
}

.next-trips__list-wrapper {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $mobile-max-width) {
    justify-content: space-between;
  }
}

.next-trips__list {
  @include reset-style-list;

  width: 129px;

  @media (max-width: $tablet-max-width) {
    width: 32%;
  }

  @media (max-width: $mobile-max-width) {
    width: 47%;
  }
}

.next-trips__list:last-child {
  @media (max-width: $mobile-max-width) {
    width: 100%;
  }
}

.next-trips__list:not(:last-child) {
  @media (max-width: $mobile-max-width) {
    margin-bottom: 10px;
  }
}

.next-trips__item {
  margin-bottom: 8px;
}

.next-trips__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: rgba($darkest, 0.5);
  transition-duration: 0.3s;
}

.next-trips__link:hover {
  color: $scarlet;
}
