  margin-bottom: 20px;
  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
  color: #141517; }

.corporate-travelers__description {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: rgba(20, 21, 23, 0.5); }

.modal-buy {
  position: fixed;
  z-index: 60;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.5); }

.modal-buy__show-overlay {
  display: block; }

.modal-buy--no-js {
  position: static;
  display: block;
  width: 100%;
  margin: 0 auto;
  padding: 50px;
  height: auto;
  box-sizing: border-box;
  overflow: visible;
  background-color: rgba(0, 0, 0, 0.2); }

.modal-buy__card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 60;
  width: 728px;
  margin: 0 auto;
  padding: 72px 61px 89px 338px;
  box-sizing: border-box;
  text-align: end;
  background-color: #fbf8f8;
  background-image: url("../img/modal-desktop.jpg");
  background-repeat: no-repeat;
  background-position: start center;
  background-size: 46% 100%;
  box-shadow: 0 4px 30px rgba(41, 54, 66, 0.1);
  animation-name: modal-appearance;
  animation-duration: 1s; }
  @media (min-resolution: 2dppx) {
    .modal-buy__card {
      background-image: url("../img/modal-desktop@2x.jpg");
      background-size: 46% auto; } }
  @media (max-width: 767px) {
    .modal-buy__card {
      width: 320px;
      padding: 100px 16px 185px 16px;
      text-align: center;
      background-color: #ffffff;
      background-image: none; } }

.modal-buy--no-js .modal-buy__card {
  position: static;
  display: block;
  animation: none;
  transform: translate(0, 0); }

.modal-buy__title {
  width: 237px;
  margin: 0;
  margin-bottom: 50px;
  padding-left: 74px;
  font-weight: 600;
  font-size: 25px;
  line-height: 170%;
  color: #48494d;
  text-align: center; }
  @media (max-width: 767px) {
    .modal-buy__title {
      width: 288px;
      margin: 0 auto;
      margin-bottom: 50px;
      padding: 0; } }

.modal-buy__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  padding-left: 59px; }
  @media (max-width: 767px) {
    .modal-buy__list {
      padding-left: 0; } }

.modal-buy__item {
  width: 270px; }
  @media (max-width: 767px) {
    .modal-buy__item {
      width: 288px; } }

.modal-buy__item:not(:last-child) {
  margin-bottom: 20px; }

.modal-buy__input {
  width: 270px;
  padding: 16px 15px 15px 15px;
  box-sizing: border-box;
  font: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #48494d;
  border: 1px solid #d7d9df;
  border-radius: 0; }
  @media (max-width: 767px) {
    .modal-buy__input {
      width: 288px; } }

.modal-buy__input:hover {
  border: 1px solid rgba(241, 125, 75, 0.3); }

.modal-buy__input--tel {
  padding: 16px 15px 15px 26px; }

.modal-buy__tel-wrapper {
  position: relative; }

.modal-buy__tel-wrapper::after {
  position: absolute;
  top: 7px;
  left: 14px;
  content: "";
  width: 6px;
  height: 27px;
  margin-right: 5px;
  background-image: url("../img/icon-star.svg");
  background-repeat: no-repeat;
  background-position: center; }

.modal-buy__close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  background-image: url("../img/icon-close-cross.svg");
  background-repeat: no-repeat;
  border: none;
  cursor: pointer; }

.modal-buy--no-js .modal-buy__close {
  display: none; }

.modal-buy__close:hover {
  transform: scale(1.2); }

@keyframes modal-appearance {
  0% {
    transform: scale(0) translate(-50%, -50%); }
  100% {
    transform: scale(1) translate(-50%, -50%); } }

.modal-success {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.5); }

.modal-success__show-overlay {
  display: block; }

.modal-success__card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 110;
  width: 728px;
  margin: 0 auto;
  padding: 176px 164px;
  box-sizing: border-box;
  text-align: center;
  background: #fbf8f8;
  box-shadow: 0 4px 30px rgba(41, 54, 66, 0.1); }
  @media (max-width: 767px) {
    .modal-success__card {
      width: 288px;
      margin: 0 auto;
      padding: 150px 20px; } }

.modal-success__message {
  font-weight: 500;
  font-size: 25px;
  line-height: 170%; }

.modal-success__close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  background-image: url("../img/icon-close-cross.svg");
  background-repeat: no-repeat;
  border: none;
  cursor: pointer; }

.modal-success__close:hover {
  transform: scale(1.2); }
